.main_home{
    font-family: 'Ubuntu', sans-serif;
    background: #f6f6f6;
    width: 100%;
    min-height: 120vh;
    height: 100%;
}

.mycard{
    width: 60em;
    border-radius: 5px;
    position: relative;
    display: flex;
    margin: 50px 0;
}


.mycard_img{
    width: 15em;
    margin-right: 20px;
    border-radius: 5px;
    display: flex;
}

.dateHome{
    position: absolute;
    right: 2%;
    bottom: 2%;
    font-size: 0.9em;
    font-weight: 700;
}

.inner-date{
    font-size: 1.3em;
    color: rgba(143, 143, 143, 0.911);
}


#mycard_btn{
    display: inline;
    border: none;
    font-size: 1em;
}

#mycard_btn:hover{
    background: none;
}

.mycard_text{
    color: rgb(112, 112, 112);
}

@media (max-width: 986px){
    .main_home{
        height: 100%;
    }

    .mycard{
        width: 18em;
        height: 100%;
        min-width: 100%;
        display: block;
        margin: 20px auto;
        box-shadow: 2px 2px 5px #dfdfdf,
                    -2px -2px 5px #dfdfdf;
        
    }

    .mycard_img{
        margin: auto;
        padding: 20px 0;
        width: 80%;
        min-width: 15em;
        /* padding: 0 25px; */
    }

    .mycard_body{
        width: 80%;
        margin: auto;
    }

    #mycard_btn{
        padding: 0;
        width: 11em;
        display: block;
        padding-bottom: 30px;
    }
    .dateHome{
        height: 7%;
        position: absolute;
        display: block;
        margin: 20px 0;
        top: 3%;
        right: 12%;
        background: #000000;
        text-align: center;
        border-radius: 5px;
    }
    .inner-date{
        margin: 0;
        width: 100%;
        padding: 2px;
        color: rgb(224, 224, 224);
    }
}
#navbar{
    border-bottom: 1px solid rgba(80, 80, 80, 0.123);
    background: #f6f6f6;
}

#brandName{
    font-size: 2em;
    font-weight: 600;
    font-family: 'Krona One', sans-serif;
}

#navLinks{
    color: rgb(128, 128, 128);
    font-size: 1.3em;

}

.main_create {
  font-family: "Ubuntu", sans-serif;
  background: #f6f6f6;
}

#form {
  width: 100%;
  height: 100vh;
  position: relative;
  margin: auto;
  background: #f6f6f6;
}

.title_create {
  font-size: 4em;
  margin: 30px auto;
}

.titleInput {
  /* width: 700px; */
  height: 80px;
  font-size: 80px;
  border: none;
  outline: none;
  margin: 20px 0;
  background: #f6f6f6;
}

.descInput {
  height: 200px;
  font-size: 50px;
  border: none;
  outline: none;
  margin: 20px 0;
  background: #f6f6f6;
}

.imageInput {
  font-size: 40px;
  border: none;
  width: 500px;
  outline: none;
  margin: 20px 0;
  background: #f6f6f6;
}

#dateSelector {
  width: 500px;
  height: 60px;
  font-size: 1.5em;
  border: none;
  background: #f6f6f6;
}

#btn_create {
  width: 100px;
  background: rgb(45, 100, 252);
  color: #fff;
  position: absolute;
  top: -12%;
  right: 5%;
}

@media only screen and (max-width: 1200px) {
  #btn_create {
    top: -2%;
    right: 0;
  }
}
@media only screen and (max-width: 767px) {
  .title_create {
    font-size: 3em;
  }

  .titleInput {
    height: 50px;
    font-size: 50px;
  }

  .descInput {
    height: 180px;
    font-size: 40px;
  }

  .imageInput {
    font-size: 40px;
    width: 50%;
  }

  #dateSelector {
    width: 50%;
  }

  #btn_create {
    top: -10%;
    right: 0;
  }
}

@media only screen and (max-width: 375px) {
  .title_create {
    font-size: 2.5em;
  }

  #btn_create {
    right: 0;
  }
}

.main_blog {
  font-family: "Ubuntu", sans-serif;
  background: #f6f6f6;
  min-height: 210vh;
}

.mainblog_body {
  margin: 30px 0 0 0;
  position: relative;
}

.title {
  font-size: 4rem;
  text-align: center;
}

.dateMain {
  font-size: 0.8em;
  margin: 0;
  padding: 0;
  text-align: center;
}

.image {
  width: 100%;
  height: 90vh;
  border-radius: 5px;
  margin: 20px 0;
}

.description {
  line-height: 35px;
  font-size: 1.5em;
}

#btn1 {
  position: absolute;
  top: 6.5%;
  right: 12%;
  color: rgba(7, 255, 19, 0.808);
}

#btn2 {
  position: absolute;
  top: 6.5%;
  right: 0%;
  color: rgba(199, 25, 25, 0.781);
}

.icons {
  display: none;
}

@media only screen and (max-width: 1200px) {
  #btn1 {
    top: 12%;
    right: 17%;
  }

  #btn2 {
    top: 12%;
    right: 0%;
    color: rgba(199, 25, 25, 0.781);
  }
}

@media only screen and (max-width: 992px) {
  #btn1 {
    top: 14%;
    right: 22%;
  }

  #btn2 {
    top: 14%;
  }
}

@media only screen and (max-width: 992px) {
  .image {
    width: 100%;
    height: 50%;
  }
  .btns {
    display: none;
  }
  .icons {
    display: block;
  }
  #icon1 {
    position: absolute;
    top: 14%;
    right: 12%;
    width: 49px;
    height: 49px;
  }
  #icon2 {
    position: absolute;
    top: 14%;
    right: 0;
    width: 50px;
    height: 50px;
  }
}

@media only screen and (max-width: 491px) {
  #icon1 {
    top: 13%;
    right: 12%;
  }
  #icon2 {
    top: 13%;
    right: 0;
  }
}

@media only screen and (max-width: 458px) {
  #icon1 {
    top: 16%;
    right: 12%;
  }
  #icon2 {
    top: 16%;
    right: 0;
  }
}

@media only screen and (max-width: 444px) {
  .title {
    font-size: 2.5rem;
    text-align: center;
  }
  .image {
    width: 100%;
    height: 50%;
  }
  #icon1 {
    top: 6.5%;
    right: 12%;
  }
  #icon2 {
    top: 6.5%;
    right: 0;
  }
}

@media only screen and (max-width: 378px) {
  .title {
    font-size: 2.5rem;
    text-align: center;
  }
  #icon1 {
    top: 7%;
    right: 13%;
    color: "red";
  }
  #icon2 {
    top: 7%;
    right: 0;
  }
}

